.header {
    position: fixed;
    z-index: var(--z-fixed);
    top: 0;
    padding: .5rem;
    padding-top: var(--header-height);
    background-color: var(--second-color);
    width: 50%;
    height: 100vh;
    margin-left: -100vw;
    transition: margin-left .5s;
    overflow-y: auto;

    @include mq(m) {
        margin-left: 0;
        width: 100%;
        padding-top: 0;
        height: auto;
        display: grid;
        grid-template-areas: 
            "logo title menu"
            "logo title social";
        grid-template-columns: var(--header-height) 1fr 2fr;
        grid-template-rows: repeat(2, calc(var(--header-height) / 2));
        grid-gap: 1vw;

    }

    @include mq(l) {
        position: fixed;
        padding: 1rem;
        width: 30%;
        height: 100vh;
        display: flex;
        flex-direction: column;

    }

    &.is-active {
        margin-left: 0;
    }

    &--btn {
        position: fixed;
        z-index: var(--z-modal);
        top: var(--logo-spacing);
        right: var(--logo-spacing);
        display: flex;
        align-items: center;
        width: var(--logo-width);
        height: var(--logo-height);
        background-color: var(--second-color);

        @include mq(m) {display: none; }

        .hamburger-inner {
            &,
            &:after,
            &:before {
                background-color: var(--link-color);
                width: 100%;
            }
        }

    }
}